.main {
	position: relative;
	width: calc(min(var(--max-width), 95vw));
	margin-top: var(--spacer-8);
	border-radius: var(--br);
	background-color: var(--clr-main);
	overflow: hidden;
	.form-body {
		padding: var(--spacer-3) var(--spacer-5);

		@media (min-width: 50rem) {
			padding: var(--spacer-5) var(--spacer-8);
		}
	}
}
